import { FC } from 'react';
import { StepNumber, StyledSmartQueueingStep } from './styled';
import React from 'react';
import { Text } from '../common';

export const SmartQueueingStep: FC<{
  id: string;
  stepNumber: number;
  children: React.ReactNode;
}> = ({ stepNumber, children, id }) => {
  return (
    <StyledSmartQueueingStep id={id}>
      <StepNumber>
        <Text.Regular.Normal.sm>{stepNumber}</Text.Regular.Normal.sm>
      </StepNumber>
      {children}
    </StyledSmartQueueingStep>
  );
};
