import * as React from 'react';
import { Divider, Layout, Text } from '../components/common';
import {
  GatsbySeoComponent,
  MetaData,
  TopLevelPageJSONLDSchemas,
} from '../components/common/SEO';
import { useInView } from 'react-cool-inview';
import featureImage from '../images/GetDemo/featureImage.png';
import { StyledGetDemoContainer } from '../components/common/GetDemoForm/styled';
import { SmartQueueingSchema } from '../components/smart-queueing/schemaComponent';
import { PlayBackButton, TitleContainer } from '../components/smart-queueing/styled';
import { useState } from 'react';
