import React, { useEffect, useState } from 'react';
import { ButtonContainer, CalculatorRow, StyledCalculator } from './styled';
import { NumberInput, Text } from '../common';
import { TextInput } from '../common/Form';
import { PrimaryButton } from '../common/Buttons/Buttons';
import { CalculatorDataForm } from './calculatorDataForm';

const SmartQueueingCalculator = () => {
  const [showForm, setShowForm] = useState(false);
  const [messagingVolume, setMessagingVolume] = useState(0);
  const [platformFeesCost, setPlatformFeesCost] = useState(0);
  const [annualRevealedLost, setAnnualRevealedLost] = useState(0);
  const [monthRevealedLost, setMonthRevealedLost] = useState(0);

  console.log(messagingVolume / 10);
  const messagesLost = messagingVolume / 10;
  const passThroughLost = messagesLost * 0.003;
  const platformLost = platformFeesCost * messagesLost;
  const monthLost = passThroughLost + platformLost;
  const annualLost = monthLost * 12;
  console.log(platformFeesCost);

  const onSubmit = () => {
    setAnnualRevealedLost(annualLost);
    setMonthRevealedLost(monthLost);
  }

  return (
    <StyledCalculator>
      <Text.Title.Bold.sm>Smart Queueing Calculator</Text.Title.Bold.sm>
      <Text.Regular.Bold.md>Cost of Failed Messages</Text.Regular.Bold.md>
      <CalculatorRow>
        <div>
          <Text.Regular.Normal.md>Monthly Messaging Volume</Text.Regular.Normal.md>
        </div>
        <div>
          <NumberInput onChange={(value) => setMessagingVolume(value)} />
        </div>
      </CalculatorRow>
      <CalculatorRow>
        <div>
          <Text.Regular.Normal.md> Platform Fees Cost</Text.Regular.Normal.md>
          <Text.Regular.Normal.sm>(What You Pay Per-Message)</Text.Regular.Normal.sm>
        </div>
        <div>
          <TextInput
            onChange={(e) => setPlatformFeesCost(e.target.value)}
            type="number"
          />
        </div>
      </CalculatorRow>
      <CalculatorRow>
        <div>
          <Text.Regular.Normal.md>
            10% of Total Messages Are Expected to Drop Each Month
          </Text.Regular.Normal.md>
        </div>
        <div>
          <NumberInput disabled initialValue={Math.round(messagesLost)} />
        </div>
      </CalculatorRow>
      <CalculatorRow>
        <div>
          <Text.Regular.Normal.md>
            $ Spent on Carrier Pass-through Fees for Failed Messages
          </Text.Regular.Normal.md>
          <Text.Regular.Normal.sm>(Approx. $0.003 Per-Message)</Text.Regular.Normal.sm>
        </div>
        <div>
          <TextInput disabled value={`$${passThroughLost.toFixed(2)}`} />
        </div>
      </CalculatorRow>
      <ButtonContainer>
        <PrimaryButton
          onClick={() => setShowForm(true)}
          disabled={!messagingVolume || !platformFeesCost}
        >
          Calculate
        </PrimaryButton>
      </ButtonContainer>
      <Text.Regular.Bold.md>Potential Lost Revenue</Text.Regular.Bold.md>
      <CalculatorRow>
        <div>
          <Text.Regular.Normal.md>Monthly</Text.Regular.Normal.md>
        </div>
        <div>
          <TextInput disabled value={`${monthRevealedLost ? "$" + monthRevealedLost.toFixed(2) : '-'}`} />
        </div>
      </CalculatorRow>
      <CalculatorRow>
        <div>
          <Text.Regular.Normal.md>Annually</Text.Regular.Normal.md>
        </div>
       <div>
          <TextInput disabled value={`${annualRevealedLost ? "$" + annualRevealedLost.toFixed(2) : '-'}`} />
        </div>
      </CalculatorRow>
      {showForm && (
        <CalculatorDataForm
          onClose={() => setShowForm(false)}
          montlyPotentialLostRevenue={String(monthLost)}
          annualPotentialLostRevenue={String(annualLost)}
          platformFee={String(platformFeesCost)}
          messagingVolume={String(messagingVolume)}
          onSubmit={onSubmit}
        />
      )}
    </StyledCalculator>
  );
};

export default SmartQueueingCalculator;
